@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700);
body{
    width: 100%;
    height: 100%;
    max-height: 100%;
    font-family: 'Roboto', sans-serif;
    background-color: #ffffff;
    padding: 0;
    margin: 0;
    letter-spacing: 0.2px;
}
.noPad{padding: 0;}
.pointer{cursor: pointer;}
.amcharts-chart-div > svg > g > g > text{
    font-family: 'Roboto', serif;
    fill: #97979a;
}
.arrow{
    border: solid #707070;
    border-width: 0 3px 3px 0;
    display: inline-block;
    padding: 3px;
}
.arrow:hover{
    border-color: #5f5f5f;
}
.upArrow{
    transform: rotate(-135deg);
    -webkit-transform: rotate(-135deg);
}
.downArrow{
    transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
}

.datePickerHold{
    display: inline-block;
    font-size: 12px;
    font-weight: 500;
    font-style: normal;
    font-stretch: normal;
    line-height: 20px;
    letter-spacing: normal;
    color: #707070;
    float: right;
}
.datePickerHold > .inpCheckBox{
    margin-right: 30px;
    position: relative;
    cursor: pointer;
    top: 1px;
    font-size: 14px;
    font-weight: 500;
    font-style: normal;
    font-stretch: normal;
    line-height: 20px;
    letter-spacing: normal;
    color: #707070;
}
.datePickerHold > .inpCheckBox > input[type='checkbox']{
    position: relative;
    cursor: pointer;
    top: 3px;
    height: 16px;
    width: 16px;
}
.datePickerHold > .datePickerBtn{
    width: 72px;
    height: 24px;
    line-height: 24px;
    margin: 0 0 0 10px;
    text-align: center;
    font-size: 12px;
    font-style: normal;
    font-stretch: normal;
    letter-spacing: normal;
    border-radius: 3px;
    background-color: #707070;
    padding: 0 0 0 0;
    border: 1px solid #707070;
    font-weight: 500;
    color: #FFFFFF;
    outline: none;
    cursor: pointer;
}
.datePickerHold > .datePickerBtn:hover{
    background-color: #5f5f5f;
}
.datePickerHold > *{
    display: inline-block;
}
.datePickerHold > div > .react-datepicker-wrapper > .react-datepicker__input-container > input,
.datePickerHold > div > .react-datepicker-wrapper > .react-datepicker__input-container > input{
    width: 120px;
    height: 20px;
    line-height: 20px;
    text-align: center;
    font-size: 12px;
    font-style: normal;
    font-stretch: normal;
    letter-spacing: normal;
    border-radius: 3px;
    background-color: #ffffff;
    padding: 0 0 0 0;
    border: 1px solid #707070;
    font-weight: 500;
    color: #707070;
    outline: none;
    cursor: pointer;
    margin-left: 0.2px;
    margin-right: 0.3px;
}
.datePickerHold > div > .react-datepicker-wrapper > .react-datepicker__input-container > input:hover,
.datePickerHold > div >  .react-datepicker-wrapper > .react-datepicker__input-container > input:hover{
    color: #404040;
    background-color: #d9d9d9;
}
.datePickerHold .react-datepicker__month-container{
    font-size: 10px;
}
.datePickerHold.shiftPicker > div > .react-datepicker-popper{
    left: -60px !important;
}
.datePickerHold.shiftPicker > div > .react-datepicker-popper > .react-datepicker > .react-datepicker__triangle{
    margin-left: 40px !important;
}
.datePickerHold .react-datepicker__time-container .react-datepicker__time .react-datepicker__time-box ul.react-datepicker__time-list{
    padding: 0;
}
.datePickerHold .react-datepicker__time-container .react-datepicker__time .react-datepicker__time-box ul.react-datepicker__time-list li{
    font-size: 12px;
}

@media screen and (max-width: 1800px) {
    .datePickerHold {
        font-size: 12px;
    }
    .datePickerHold > .datePickerSearch {
        width: 72px;
        height: 24px;
        line-height: 24px;
        margin: 0 0 0 10px;
        font-size: 11px;
    }
    .datePickerHold > div > .react-datepicker-wrapper > .react-datepicker__input-container > input,
    .datePickerHold > div > .react-datepicker-wrapper > .react-datepicker__input-container > input {
        width: 120px;
        font-size: 11px;
        height: 20px;
        line-height: 20px;
    }
    .datePickerHold .react-datepicker__time-container .react-datepicker__time .react-datepicker__time-box ul.react-datepicker__time-list li{
        font-size: 12px;
    }
}
@media screen and (max-width: 1275px){
    .datePickerHold.shiftPicker > div > .react-datepicker-popper{
        left: -60px !important;
    }
    .datePickerHold.shiftPicker > div > .react-datepicker-popper > .react-datepicker > .react-datepicker__triangle{
        margin-left: 40px !important;
    }
}
/*CSS FOR SCROLL BAR BEGIN HERE*/
::-webkit-scrollbar {
    width: 6px;
    height: 6px;
}
::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.4);
}
::-webkit-scrollbar-thumb {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.4);
}

#notificationBtn ::-webkit-scrollbar-thumb {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.4);
    background-color: #637891;
}
/*CSS FOR SCROLL BAR END HERE*/

.pageView{
    position: absolute;
    top:60px;
    left: 100px;
    width: calc(100% - 100px);
    height: calc(100% - 60px);
    background-color: #ffffff;
    padding: 0;
    margin: 0;
    min-width: 562px;
    -webkit-transition: 0.5s;
    transition: 0.5s;
    overflow-y: auto;
}
@media screen and (min-width: 1400px) and (max-width: 1600px) {
    .pageView {
        left: 90px;
        width: calc(100% - 90px);
    }
}
@media screen and (max-width: 1400px) {
    .pageView{
        left: 100px;
        width: calc(100% - 100px);
        top: 60px;
        min-height: calc(100% - 60px);
    }
}
@media screen and (max-width: 768px) {
    .pageView{
        left: 80px;
        width: calc(100% - 80px);
        top: 40px;
        min-height: calc(100% - 40px);
    }
}

.thisTable{
    padding: 0;
    height: 100%
}
.thisTable > .tbl-header{
    background-color: #f2f3f4;
}
.thisTable > .tbl-content{
    max-height: calc(100% - 40px);
    overflow-y:auto;
    margin-top: 0;
    background-color: #ffffff;
    color: #707070;
}
.thisTable > .tbl-header > table,
.thisTable > .tbl-content > table{
    font-size: 20px;
    width:100%;
    table-layout: fixed;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: normal;
    border-left : 1px solid #dcddde;
    border-top  : 1px solid #dcddde;
    
    border-spacing: 0;
    
}
.thisTable > .tbl-header > table > thead > tr,
.thisTable > .tbl-content > table > tbody > tr{
    border-bottom: 1px solid #dcddde;
    padding: 0;
    word-break: break-all;
    

}

.thisTable > .tbl-header > table > thead > tr >  th{
    font-weight: 500;
    padding: 8px 5px;
    color: #52535e;
    text-align: left;
}
.thisTable > .tbl-content > table > tbody > tr > td{
    padding: 8px 5px;
    color: #52535e;
    word-break: break-all;
    text-align: left;
    border-bottom: 1px solid #dcddde;
    
}


.thisTable > .tbl-content > table > tbody > tr:last-child > td{
    border-bottom: none;
}
.thisTable > .tbl-header > table > thead > tr >  th.center,
.thisTable > .tbl-content > table > tbody > tr > td.center{
    text-align: center;
}
.thisTable > .tbl-content > table > tbody > tr > td.noData{
    text-align: center;
    height: 100px;
}
.thisTable > .tbl-content > table > tbody > tr > td > a,
.thisTable > .tbl-content > table > tbody > tr > td a{
    text-decoration: none;
    cursor: pointer;
    color: #00007e;
}
.thisTable > .tbl-content > table > tbody > tr > td a:hover{
    text-decoration: underline;
}

@media screen and (min-width: 1600px) and (max-width: 1800px)  {
    .thisTable > .tbl-content{
        height: calc(100% - 33px);
    }
    .thisTable > .tbl-header > table,
    .thisTable > .tbl-content > table{
        font-size: 18px;
    }
    .thisTable > .tbl-header > table > thead > tr >  th,
    .thisTable > .tbl-content > table > tbody > tr > td{
        padding: 7px 3px 7px 7px;
    }
}
@media screen and (min-width: 1400px) and (max-width: 1600px)  {
    .thisTable > .tbl-content{
        height: calc(100% - 33px);
    }
    .thisTable > .tbl-header > table,
    .thisTable > .tbl-content > table{
        font-size: 16px;
    }
    .thisTable > .tbl-header > table > thead > tr >  th,
    .thisTable > .tbl-content > table > tbody > tr > td{
        padding: 6px 3px 6px 7px;
    }
}
@media screen and (min-width: 1260px) and (max-width: 1400px)  {
    .thisTable > .tbl-content{
        height: calc(100% - 29px);
    }
    .thisTable > .tbl-header > table,
    .thisTable > .tbl-content > table{
        font-size: 14px;
    }
    .thisTable > .tbl-header > table > thead > tr >  th,
    .thisTable > .tbl-content > table > tbody > tr > td{
        padding: 5px 2px 5px 6px;
    }
}
@media screen and (min-width: 950px) and (max-width: 1260px)  {
    .thisTable > .tbl-content{
        height: calc(100% - 25px);
    }
    .thisTable > .tbl-header > table,
    .thisTable > .tbl-content > table{
        font-size: 13px;
    }
    .thisTable > .tbl-header > table > thead > tr >  th,
    .thisTable > .tbl-content > table > tbody > tr > td{
        padding: 4px 2px 4px 6px;
    }
}
@media screen and (max-width: 950px)  {
    .thisTable > .tbl-content{
        max-height: calc(100% - 25px);
    }
    .thisTable > .tbl-header > table,
    .thisTable > .tbl-content > table{
        font-size: 12px;
    }
    .thisTable > .tbl-header > table > thead > tr >  th,
    .thisTable > .tbl-content > table > tbody > tr > td{
        padding: 4px 2px 4px 6px;
    }
}

.paginationRow{
    width: 100%;
    display: block !important;
}
.paginationRow > table{
    border-spacing: 0;
}
.paginationRow .paginationTD{
    outline: none;
    margin: 0;
    vertical-align: middle;
    border-width: 2px 1px;
    border-color: #52535e;
    border-style: solid;
    cursor: pointer;
    background-color: #ffffff;
    text-decoration: none;
    color: #52535e;
    padding-right: 5px;
    min-width: 30px;
}
.paginationRow .paginationTD  a{
    margin-left: 10px;
    margin-right: 10px;
    font-weight: 500;
}
.paginationRow .tdPadding1{
    font-size: 14px;
    padding: 3px;
}
.paginationRow .tdPadding2{
    font-size: 14px;
    padding: 4px;
}
.paginationRow .paginationTD a:hover{
    text-decoration: none;
    outline: none;
}
.paginationRow .edges{
    color:#52535e;
    font-size: 14px;
}
.paginationRow .firstPage{
    border-radius: 5px 0 0 5px;
    border-left: 2px solid #52535e;
}
.paginationRow .lastPage{
    border-radius: 0 5px 5px 0;
    border-right: 2px solid #52535e;
}
.paginationRow .activePage{
    background-color: #52535e;
}
.paginationRow .activePage a{
    color: #ffffff;
}
.paginationRow .paginationText{
    font-size: 14px;
    padding: 4px 3px 3px 10px;
    font-style: italic;
    font-weight: bold;
    color: #52535e
}
@media screen and (min-width: 1400px) and (max-width: 1600px){
    .paginationRow .paginationTD{
        min-width: 25px;
    }
    .paginationRow .paginationTD  a{
        margin-left: 7px;
        margin-right: 7px;
    }
    .paginationRow .tdPadding1{
        font-size: 13px;
    }
    .paginationRow .tdPadding2{
        font-size: 12px;
    }
    .paginationRow .edges{
        font-size: 12px;
    }
    .paginationRow .paginationText{
        font-size: 13px;
        padding: 4px 3px 3px 7px;
    }
}
@media screen and (max-width: 1400px){
    .paginationRow .paginationTD{
        min-width: 20px;
    }
    .paginationRow .paginationTD  a{
        margin-left: 5px;
        margin-right: 5px;
    }
    .paginationRow .tdPadding1{
        font-size: 12px;
    }
    .paginationRow .tdPadding2{
        font-size: 10px;
    }
    .paginationRow .edges{
        font-size: 10px;
    }
    .paginationRow .paginationText{
        font-size: 12px;
        padding: 4px 3px 3px 5px;
    }
}

.pageView > hr{
    width: calc(100% - 60px);
    height: 1px;
    background-color: #cbcbcb;
    border: none;
}
.pageView > .pageRow{
    padding: 30px;
    width: calc(100% - 60px);
