    font-size: 25px;
    color: #FFFFFF;
    cursor: pointer;
    background-image: -webkit-linear-gradient(top, #f9bdaf, #f08877);
    background-image: linear-gradient(to bottom, #f9bdaf, #f08877);
}
.pageView > .pageRow > .thisBlock > .blockTitle > .addBtn{
    margin-top: 5px;
