.dialogBox{
    border: 2px solid transparent;
    background-color: transparent;
    width: 680px;
    top: 200px;
    padding: 0;
    overflow-x: hidden;
    border-radius: 5px;
    overflow-y: hidden;
    box-shadow: 0 1px 1px 0 rgba(122, 122, 122, 0.5);
}
.dialogBox > .dialogTitle{
    height: 50px;
    line-height: 50px;
    background-color: #54565d;
    padding: 0 30px;
    font-size: 22px;
    font-weight: 300;
    font-style: normal;
    font-stretch: normal;
    letter-spacing: normal;
    color: #ffffff;
}
.dialogBox > .dialogTitle > button{
    float: right;
    cursor: pointer;
    background: transparent;
    border: none;
    outline: none;
    box-shadow: none;
    color: #FFFFFF;
    line-height: 50px;
}
.dialogBox > .dialogBody{
    background-color: #f2f3f4;
    padding: 10px 30px;
}
.dialogBox > .dialogBody > div > label{
    display: block;
    font-size: 20px;
